.vc-header {

  background-image: -webkit-linear-gradient(#D2EFF7 0%, #ADE5F6 46%, #ffffff 50%,#071E58  54%, #379FBF 58%, #2e85bf 90%, #0b2b40 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  -webkit-text-stroke: 2px #f5f5f5;
  font-family: 'Titillium Web', sans-serif;
  font-style: italic;
  margin: 0;
  line-height: .1;
  text-align: center;
 
  font-family: 'Shadows Into Light';
  color: white;
  font-size: calc(5rem + 3.6vw);
  
  line-height: 5rem;
  text-shadow: 2px 2px 15px #feffb3;
}

